<template>
	<view>
		<view
			style="width: 100%;padding-left: 12px;height: 48px;line-height: 48px;color: #323232;font-size: 16;font-weight: bold;">
			充值金额
		</view>
		<view class="row" style="width: calc(100% - 24px);margin-left: 12px;margin-top: 8px; flex-wrap: wrap;">
			<view v-for="(item,index) in payList" :key="index" class="pay-item"
				:style="index%3 == 2 ? 'margin-right:0px':'' " @click="itemCLick(item,index)">
				¥{{item.money}}
			</view>
		</view>
		<view class="pay-input-back">
			<input type="number" :value="money" placeholder="输入其他充值金额"
				style="width: 50%;margin-left: 12px;height: 40px;line-height: 40px;font-size: 14px;" @input="input" />
			<text
				style="position: absolute;right: 0;height: 40px;line-height: 40px;font-size: 14px;top: 0;width: 30px;">元</text>
		</view>
		<view :enabled="false" :class="{opatity: enabled}" class="submit-wrap" @click="subyijian">
			立即充值
		</view>
		<payView v-if="showpay" :money="money" type="1" @closePayView="closePayView"></payView>
	</view>
</template>

<script>
	import payView from '../../../components/payview/payview.vue'
	export default {
		components: {
			payView
		},
		data() {
			return {
				payList: ['50', '100', '150', '200', '250'],
				money: '',
				showpay: 0
			}
		},
		computed: {
			enabled: function() {
				if (this.money == '') {
					return true;
				}
				return false;
			}
		},
		onLoad() {
			var that = this
			this.http.sendRequest('Charge.GetRule',{}).then(function(res){
				if (res.code == 0) {
					console.log(res.info)
					that.payList = res.info
				}
			})
			

		},
		methods: {
			itemCLick(item, index) {
				this.money = item.money
			},
			input(e) {
				this.money = e.detail.value
			},
			subyijian() {
				this.showpay = 1
			},
			closePayView(e) {
				console.log(e)
				this.showpay = 0
				if (e != 0) {
					this.http.sendRequest('Charge.Buy', {
						'money': this.money,
						// #ifdef APP-PLUS
						'payid': e.id,
						// #endif
						// #ifdef MP-WEIXIN
						'openid': uni.getStorageSync('openid'),
						'payid': e.id == 2 ? '3' : e.id,
						// #endif
					}).then(function(res) {
						if (res.code == 0) {
							// #ifdef APP-PLUS
							if (e.id == 1) {
								uni.requestPayment({
									provider: 'alipay',
									orderInfo: res.info[0].ali.orderinfo, //微信、支付宝订单数据 【注意微信的订单信息，键值应该全部是小写，不能采用驼峰命名】
									success: function(res) {
										uni.showToast({
											title:'支付成功',
											duration: 2000
										})
										console.log('success:' + JSON.stringify(res));
									},
									fail: function(err) {
										console.log('fail:' + JSON.stringify(err));
									}
								});
							} else if (e.id == 2) {
								uni.requestPayment({
									"provider": "wxpay",
									// #ifdef APP-PLUS
									"orderInfo": res.info[0].wx,
									// #endif
									// #ifdef MP-WEIXIN
									    'timeStamp': res.info[0].small.timeStamp,
									    'nonceStr': res.info[0].small.nonceStr,
									    'package': res.info[0].small.package,
									    'signType': res.info[0].small.signType,
									    'paySign': res.info[0].small.sign,
									// #endif
									success(res) {
										uni.showToast({
											title:'支付成功',
											duration: 2000
										})
										console.log(res)
										
									},
									fail(e) {
										console.log(e)
										
									}
								})
							}
							// #endif
							
						}
					})
				}

			}

		}
	}
</script>

<style>
	page {
		width: 100%;
		height: 100%;
	}

	.pay-item {
		display: inline-block;
		width: calc(33.3% - 16px);
		height: 40px;
		background-color: #F4F4F4;
		color: #646464;
		font-size: 16px;
		line-height: 40px;
		text-align: center;
		border-radius: 4px;
		margin-right: 24px;
		margin-bottom: 16px;
	}

	.pay-input-back {
		height: 40px;
		color: #323232;
		width: calc(100% - 24px);
		font-size: 16px;
		line-height: 40px;
		border-radius: 4px;
		margin-left: 12px;
		margin-top: 15px;
		border: 1px solid #7900B2;
		position: relative;
	}

	/* 提交 */
	.submit-wrap {
		height: 96rpx;
		line-height: 96rpx;
		width: calc(100% - 24px);
		color: #FFFFFF;
		font-size: 30rpx;
		text-align: center;
		border-radius: 4px;
		position: fixed;
		margin-left: 12px;
		margin-top: 50px;
		background-color: #7900B2;
	}

	.opatity {
		opacity: 0.5;
	}
</style>
